.offline-info{
  position: fixed;
  bottom: 0;
  width: var(--screen-sm);
  background-color: var(--color-yellow-500);
  padding: var(--spacing-2) var(--spacing-4);
  border-radius: var(--border-radius-md);
  text-align: center;
}

/* custom scroll */
/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #666; 
}

.loading{
  padding: var(--spacing-2);
  text-align: center;
}

/* Page Home */
.page {
  font-family: var(--font-family);
  background-color: var(--color-white);
  color: var(--color-black);
  max-width: var(--screen-sm);
  margin: var(--spacing-0) auto;
}

.page-title {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  padding: var(--spacing-0) var(--spacing-6);
  text-transform: capitalize;
  position: fixed;
  top: 0;
  z-index: 1;
  background-color: var(--color-white);
  padding: var(--spacing-4) var(--spacing-6);
  width: 100%;
}

.page-content {
  margin-top: var(--spacing-12);
  padding: var(--spacing-4) var(--spacing-6);
  min-height: 1000px;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: var(--spacing-3);
  grid-row-gap: var(--spacing-3);
}

.grid-item {
  position: relative;
}

/* Page Detail */
.page-detail{
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  max-width: var(--screen-sm);
  width: 100%;
  height: 100vh;
}

.page-detail__header{
  display: flex;
}

.header-button{
  padding: var(--spacing-3) var(--spacing-4);
  cursor: pointer;
}

.header-title{
  font-weight: var(--font-weight-bold);
  text-transform: capitalize;
  padding: var(--spacing-3) var(--spacing-4);
}

.page-detail__img{
  position: relative;
}

.page-detail__img img{
  margin: auto;
  max-width: 64%;
}

.page-detail__subtitle{
  font-size: 12px;
  line-height: .5;
}

.page-detail__type{
  display: flex;
  padding: 0 var(--spacing-4);
  position: absolute;
  left: 0;
  bottom: 12px;
}

.page-detail__panel{
  --top: 48px;
  position: fixed!important;
  top: var(--top);
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: var(--screen-sm);
  height: calc(100vh - var(--top));
  padding-top: 65%;
  overflow-y: scroll;
}

.panel{
  background-color: #fff;
  background-color: var(--color-white);
  border-top-left-radius: var(--border-radius-xl);
  border-top-right-radius: var(--border-radius-xl);
  padding: var(--spacing-6) var(--spacing-6) var(--spacing-6);
  min-height: calc(100vh - var(--top));
  box-shadow: 2px 4px 15px 0 rgb(0 0 0 / 25%);
}
